
































































































.header {
    background-color: #ffffff;
    width: 100%;
}

.serch-box {
    margin: -.13rem auto;
    width: 95%;
}

.select-collage {
    margin-top: .14rem;
    height: .5rem;
    font-size: .14rem;
    line-height: .5rem;
    background-color: #ffffff;
}

.select-collage .content {
    overflow: hidden;
    float: left;
    /* 文本不换行 */
    white-space: nowrap;
    /* 超出部分显示... */
    text-overflow: ellipsis;
    width: 95%;
}

.c1-ly {
    margin-left: .15rem;
}

.c-ly {
    margin-left: .05rem;
}

.choice {
    float: left;
}

.sort {
    margin-top: .06rem;
    height: .5rem;
    font-size: .14rem;
    color: #7A8F8B;
    line-height: .5rem;
    background-color: #ffffff;
}

.sort .content {
    margin-left: 5%;
    float: left;
}

.sort .sort-ico {
    float: right;
    margin-right: 2%;
}


/* 卡片盒子 */

.card-box {
    /* 有些bug */
    /*  display: flex;
    margin-top: .05rem;
    flex-wrap: wrap;
    justify-content: center; */
    /* float: left; */
    overflow: hidden;
    margin: .04rem auto;
}

.clg-card {
    float: left;
    position: relative;
    left: 2%;
    height: 1.25rem;
    width: 46%;
    margin: .04rem .04rem;
    padding: .08rem .08rem;
    background-color: #fff;
}

.clg-card .clg-logo {
    height: .70rem;
    border-bottom: 1px solid #efefef;
    /* padding-bottom: .2rem; */
}

.clg-card .cname {
    padding-top: .05rem;
    font-size: .13rem;
}

.clg-card .ename {
    padding-top: .05rem;
    font-size: .13rem;
}



